@use 'base/variables' as v;
@use 'base/mixins' as m;

.panel {
    padding: 4rem 2rem;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;

    &__contenedor {
        background-color: #121f38;
        padding: 3rem;
        border-radius: 12px;
        max-width: 600px;
        width: 100%;
        text-align: center;
        box-shadow: 0 0 20px rgba(20, 172, 199, 0.3);
    }

    &__titulo {
        font-size: 2rem;
        margin-bottom: 1rem;
        color: #ffffff;
    }

    &__descripcion {
        font-size: 1rem;
        color: #d1d5db;
        margin-bottom: 2rem;
    }

    &__boton {
        display: inline-block;
        padding: 0.75rem 1.5rem;
        font-size: 1rem;
        border: none;
        border-radius: 8px;
        text-decoration: none;
        background-color: #14ACC7;
        color: #fff;
        transition: background-color 0.3s ease;

        &:hover {
        background-color: #1097b0;
        }
    }
}