@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    padding: 5rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include m.desktop {
        flex-direction: row;
        justify-content: space-between;
    }

    h1{
        color: v.$blanco;
        font-weight: 900;
        font-size: 2.8rem;
        @include m.tablet {
            margin-bottom: 2rem;
        }

        @include m.desktop {
            margin-bottom: 0;
        }

        span{
            color: v.$azulClaro;
            text-shadow: 0 0 20px rgba(20, 172, 199, 0.6); 
        }
    }

}