}
@media (min-width: 768px) {
  .servicios__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 4rem;
  }
}
@media (min-width: 1200px) {
  .servicios__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 4rem;
  }
}

.servicio {
  background-color: rgb(10.5, 15.4, 25.2);
  box-shadow: 0px 0px 27px 0px rgba(20, 172, 199, 0.75);
